h1::after {
    content: " ";
    position: absolute;
    top: 106px;
    left: 70px;
    width: 12%;
    background-color: #fff;
    height: 4px;
}

.under-line::after {
    content: " ";
    position: absolute;
    top: 57px;
    left: 34px;
    width: 50px;
    background-color: #f23d3d;
    height: 3px;
    border-radius: 15px;
}

.line::after {
    content: " ";
    position: absolute;
    top: 95px;
    left: 15px;
    width: 50px;
    background-color: #f23d3d;
    height: 3px;
    border-radius: 15px;
}

